.professor {

  [class^='MuiDivider']{
    border-color: white;
    border-width: 1px;
    margin: 5px 0;
  }

  width: clamp(16.438rem, 2.468vw + 15.851rem, 18.813rem);
  height: clamp(21.313rem, 1.623vw + 20.927rem, 22.875rem);
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  border-radius: 15px;
  box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.19);
  background: linear-gradient(to right, rgba(232, 227, 227, 0.2), #66aaa9);
  overflow: hidden;


  &__photo {
    border-radius: 15px 15px 0 0;
    padding: {
      top: clamp(0.625rem, 0.39vw + 0.532rem, 1rem);
      bottom: 10px;
    }
  }

  &__info {
    height: 100%;
    width: 100%;
    border-radius: 0 0 15px 15px;
    background-color: #56C696;
    font-family: 'PoppinsMedium', serif;
    font-size: 18px;
    overflow-y: auto;
    overflow-x: hidden;
    padding: 10px clamp(0.5rem, 0.779vw + 0.315rem, 1.25rem);
    line-height: 24px;
    &__name {
      font-family: 'PoppinsSemiBold', serif;
    }

    &::-webkit-scrollbar{
      display: none;
    }
  }
}